<template>
	<div id="experience">
		<section id="cd-timeline" class="cd-container">
			<div class="cd-wrapper">
				<div class="cd-timeline-block" v-for="item in experience">
					<div class="cd-timeline-img cd-picture"> 
						<img :src="item.iconSrc" alt="Picture">
					</div>

					<div class="cd-timeline-content">
						<h2>
							<span>{{ item.companyName }}</span>
							<span>{{ item.projectName }}</span>
							<span>{{ item.position }}</span>
						</h2>
						<p>{{ item.description }}</p>
						<!-- <a href="#" class="cd-read-more">阅读更多</a> -->
						<span class="cd-date">{{ item.date }}</span>
					</div> 
				</div>

				<!-- <div class="cd-timeline-block">
					<div class="cd-timeline-img cd-picture">
						<img src="./../../assets/images/cd-icon-picture.svg" alt="Picture">
					</div>

					<div class="cd-timeline-content">
						<h2>html5时间表 1</h2>
						<p>jQuery 团队在官博中再次提醒用户，jQuery 2.0 不再支持IE 6/7/8 了，但是 jQuery 1.9 会继续支持。。</p>
						<span class="cd-date">Jan 14</span>
					</div> 
				</div>  -->
			</div>
		</section>
	</div>
</template>
<script>
	import './../../assets/css/timeline.css'
	import $ from 'jquery'

	//Vue中img的src属性绑定与static文件夹 
	// 参考https://www.jianshu.com/p/f82c5ecbd3a5
	let experience = [
    {
			companyName:'贵州龙马融合信息技术有限责任公司',
			position:'web前端开发',
			projectName:'紫建造工程项目管理平台',
			iconSrc:'./static/cd-icon-location.svg',
			date:'2018/7-至今',
			description:'该项目主要实现目标是：对建筑工地项目实行自动化管理，实现技术：后端：java  docker + spring cloud + mysql 前端：vue +webpack+vue-router+axios+vuex+element-ui + scss + echarts + es6 + videojs视频 + 高德地图，在此项目中我主要负责，前端框架的搭建，与全局样式的编写，负责解决项目中遇到的问题，并负责登陆及权限控制，同时负责安全管理模块，项目概况，材料管理部分功能，基础设置部分功能，二维码部分功能，BI展板部分功能，应用市场，负责前端打包交给后台上线。'
		},
		{
			companyName:'贵州衍昊信息技术有限公司',
			position:'web前端开发',
			projectName:'校园数字化管理系统',
			iconSrc:'./static/cd-icon-picture.svg',
			date:'2017/4-2018/5',
			description:'该项目的主要实现目标是对高职学院进行数字化管理,使用springboot+bootstrap技术，该系统分为基础系统、消息、教务、OA办公、学生管理、教职工管理、校企合作7个模块以及教师移动端和学生移动端。在本项目中本人独立完成学生移动端webapp的开发，整个webapp使用vue + vue-cli+webpack+axios+vux实现，数据源通过axios向后台获取。'
		},
		{
			companyName:'贵州佳网科技发展有限公司',
			position:'web前端开发',
			projectName:'表单自动化管理系统',
			iconSrc:'./static/cd-icon-movie.svg',
			date:'2016/8-2017/3',
			description:'该项目主要实现对数据表进行数字化管理，主要使用spring+mybaitis+jquery技术。在本项目中本人担任的是前端开发角色。主要负责页面，js脚本，css样式的编写工作。'
		},
		{
			companyName:'厦门赢定信息科技有限公司',
			position:'Java工程师',
			projectName:'萤火虫IT服务管理系统',
			iconSrc:'./static/cd-icon-location.svg',
			date:'2014/9-2015/12',
			description:'该系统是一个公司内部管理系统，主要的功能有员工管理，客户管理，公司业务流程管理等。在项目中主要完成了首页展示、伙伴模块等主要功能模块，采用Html ,CSS，JavaScript， JQuery等技术，后台使用Hibernate, Struts2 ,Spring3 的MVC架构，主要应用为hibernate的ORM，struts2 的OGNL, 标签库，拦截器，Spring的AOP等。'
		}
	];
	export default{
		name:'Experience',
		data(){
			return {
				"experience":experience
			}
		},
		mounted(){			
			$(function(){
				var $timeline_block = $('.cd-timeline-block');
				
				//参考:https://blog.csdn.net/xiaoya_syt/article/details/60577553
				//给伪元素添加样式
				var height = ($('.cd-wrapper').height()+20) + 'px';
				$('<style>#cd-timeline::before{height:'+height+'}</style>').appendTo('head');

				//hide timeline blocks which are outside the viewport
				$timeline_block.each(function(){
					if($(this).offset().top > $(".cd-container").scrollTop()+$(".cd-container").height()*0.25) {
						$(this).find('.cd-timeline-img, .cd-timeline-content').addClass('is-hidden');
					}
				});
				//on scolling, show/animate timeline blocks when enter the viewport
				$(".cd-container").on('scroll', function(){
					$timeline_block.each(function(){
						if( $(this).offset().top <= $(".cd-container").scrollTop()+$(".cd-container").height()*0.25 && $(this).find('.cd-timeline-img').hasClass('is-hidden') ) {
							$(this).find('.cd-timeline-img, .cd-timeline-content').removeClass('is-hidden').addClass('bounce-in');
						}
					});
				});
			});
		}
	}
</script>
<style scoped>
	.cd-timeline-block h2{
		font-size:14px;
	}
	.cd-timeline-block h2 span{
		margin-left:12px;
	}
	.cd-timeline-block h2 span:nth-child(1){
		margin-left:0;
	}
	.cd-timeline-block p{
		text-indent:2em;
	}
</style>


