<template>
	<div id="case">
		<div class="container">
			<div class="header">
				<h1 class="title">CASES</h1>
				<p class="content">案例展示</p>
			</div>
			<div id="case-swiper" class="swiper-container cases">
				<div class="swiper-wrapper">

					<div class="swiper-slide case-slide" v-for="item in caseList">
						<div class="img">
							<img :src="item.imgSrc" width="302" height="302" alt="">
						</div>
						<div class="txt">
							<h4>{{ item.projectName }}</h4>
							<p>{{ item.description }}</p>
						</div>
					</div>

					<!-- <div class="swiper-slide case-slide">
						<div class="img">
							<img src="./../../assets/student02.png" width="302" height="302" alt="">
						</div>
						<div class="txt">
							<h4>订餐管理系统5</h4>
							<p>方便饭店管理</p>
						</div>
					</div> -->
					
				</div>
			</div>
			<!-- 前进后退按钮 -->
			<div class="swiper-button-prev case-prev"></div>
			<div class="swiper-button-next case-next"></div>
		</div>
	</div>
</template>
<script>
	import Swiper from 'swiper';
	import 'swiper/dist/css/swiper.css';
	let caseList = [
    {
			projectName:'紫建造工程项目管理平台',
			imgSrc:'./static/case/project-01.png',
			description:'PC端项目概况页面'
		},
    {
			projectName:'紫建造工程项目管理平台',
			imgSrc:'./static/case/project-02.png',
			description:'PC端安全巡检页面'
		},
    {
			projectName:'紫建造工程项目管理平台',
			imgSrc:'./static/case/project-03.png',
			description:'PC端重大危险源页面'
		},
    {
			projectName:'紫建造工程项目管理平台',
			imgSrc:'./static/case/project-05.png',
			description:'PC端人员统计页面'
		},
    {
			projectName:'紫建造工程项目管理平台',
			imgSrc:'./static/case/project-06.png',
			description:'PC端应用商城页面'
		},
		{
			projectName:'校园数字化管理系统',
			imgSrc:'./static/case/school.png',
			description:'PC端'
		},
		{
			projectName:'校园数字化管理系统',
			imgSrc:'./static/case/student01.png',
			description:'学生移动端'
		},
		{
			projectName:'校园数字化管理系统',
			imgSrc:'./static/case/student02.png',
			description:'学生移动端'
		},
		{
			projectName:'校园数字化管理系统',
			imgSrc:'./static/case/student03.png',
			description:'学生移动端'
		},
		{
			projectName:'校园数字化管理系统',
			imgSrc:'./static/case/student04.png',
			description:'学生移动端'
		},
		{
			projectName:'校园数字化管理系统',
			imgSrc:'./static/case/student05.png',
			description:'学生移动端'
		},
		{
			projectName:'校园数字化管理系统',
			imgSrc:'./static/case/student05.png',
			description:'学生移动端'
		},
		{
			projectName:'订餐管理系统',
			imgSrc:'./static/case/pos.png',
			description:'方便店家管理订单'
		},
		{
			projectName:'个人网站',
			imgSrc:'./static/case/resume.png',
			description:'个人简介'
		},
	];
	export default{
		name:'Case',
		data(){
			return {
				"caseList":caseList
			}
		},
		mounted(){
			let caseSwiper = new Swiper('#case-swiper',{
				autoplay:true,
				loop:true,
				slidesPerView:3,
				spaceBetween:20,
				effect:'coverflow',
				navigation:{
	    			nextEl:'.case-next',
	    			prevEl:'.case-prev'
    			}
			});
		}
	}
</script>
<style scoped>
	h4{
		font-weight:normal;
		margin:0;
		padding:0;
	}
	#case{
		height:100%;
		width:100%;
		background-color:rgba(255,255,255,.4)
	}
	.container{
		padding-top:80px;
    /* position: relative; */
    
	}
	.header{
		/*margin-top:80px;*/
	}
	.title{
		font-size:36px;
		color:#00dfb9;
	}
	.content{
		font-size:20px;
		color:#000;
	}
	.cases{
		/*border:1px solid #f00;*/
		width:946px;
		height:380px;
		margin:20px auto;
		text-align:left;
	}
	.case-slide{
		cursor:pointer;
		background:rgba(255,255,255,.2);
	}
	.case-slide:before{
		content:'';
		position:absolute;
		top:0;
		left:0;
		display:block;
		width:302px;
		height:0;
		background:rgba(0,0,0,0);
		transition:all .25s;

	}
	.txt:after{
		content:'';
		position:absolute;
		bottom:0;
		left:0;
		display:block;
		width:302px;
		height:2px;
		background:#0ab799;
		transition:all .25s;
	}
	.case-slide:hover:before{
		height:302px;
		background:rgba(0,0,0,.5);
	}
	.case-slide:hover .txt:after{
		height:78px;
		z-index:-1;
	}
	.case-slide:hover .txt{
		color:#eee;
	}
	.case-slide:hover .txt p{
		color:#eee;
	}
	.txt{
		margin-left:18px;
		margin-top:8px;
		line-height:28px;
	}
	.txt p{
		font-size:14px;
		color:#666;
	}


	.case-next{
		right:120px;
		margin-top:18px;
	}
	.case-prev{
		left:120px;
		margin-top:18px;
	}
</style>


