<template>
	<div id="card">
		<section class="card-wrap">
			<h2 class="egname">Hello,I`m {{cardObj.egname}} </h2>
			<p class="saying">{{cardObj.saying}}</p>
			<p>我叫{{ cardObj.chname }}</p>
			<p>{{ cardObj.career }}</p>
			<p class="email">{{ cardObj.eMail }}</p>
		</section>
	</div>
</template>
<script>
	export default{
		name:'Card',
		data(){
			return {
        cardObj:{
          chname:'彭华奎',
          egname:'phk',
          saying:'人生路上没有永远的成功，只有永远的奋斗。',
          career:'前端开发工程师',
          eMail:'phkdmdm1@163.com'
        }
			}
    },
    mounted(){
      // this.getCard();
    },
    methods:{
      getCard(){
        let _self = this;
        this.$get("/card")
        .then(function(res){
          _self.cardObj = res.data;
        })
        .cath(function(err){
          console.log(err);
        })
      }
    }
	}
</script>
<style scoped>
	h2,p{
		margin:0;
		padding:0;
	}
	.card-wrap{
		position:absolute;
		top:0;
		left:0;
		right:0;
		bottom:0;
		margin:auto;
		width:700px;
		height:290px;
		color:#000;
	}
	p{
		font-size:20px;
		margin-bottom:20px;
	}
	.egname{
		font-size:40px;
		padding:15px 0 20px 0;
	}
	.saying{
		font-size:26px;
		font-weight:600;
		line-height:50px;
	}
	.email{
		color:#B22222;
	}
</style>


